import React, { useState } from "react";
import Yes from "../../images/yes.svg";
import No from "../../images/no.svg";
import "./StepThree.css";
import { Collapse, Input } from "antd";
import Clipboard from "react-clipboard.js";
import axios from "axios";
import { useHistory } from "react-router-dom";

function StepThree(props) {
  const { TextArea } = Input;
  const onSuccess = () => {
    document.body
      .getElementsByClassName("email-look")[0]
      .classList.remove("hidden");
    alert("URL 주소가 복사 되었습니다.");
    window.scrollTo(0,document.body.scrollHeight);
  };

  const { Panel } = Collapse;
  const history = useHistory();

  const [email, setEmail] = useState("");

  const onChange = e => {
    setEmail(e.target.value);
  };

  const myBirth = props.location.state.data.detail.me.birth;
  const myDay = props.location.state.data.detail.me.day;
  const myExplain = props.location.state.data.detail.me.explain;
  const taBirth = props.location.state.data.detail.ta.birth;
  const taDay = props.location.state.data.detail.ta.day;
  const taExplain = props.location.state.data.detail.ta.explain;

  const answer = props.location.state.data.answer;

  const handleClick = () => {
    axios.get(
      `https://his8t8n927.execute-api.ap-northeast-2.amazonaws.com/dev/email?my=${myBirth}&email=${email}`
    ).then(response => {
      console.log(response);
      alert(response.data.message);
    });
  };

  return (
    <div className="container">
      <br />
      <br />
      <div className="title">step 3</div>
      <br />
      <br />
      <div className="title-name">
        성격의 결과를 읽어보고
        <br />
        누구의 것인지 맞춰보세요!
      </div>
      <br />
      <br />
      <br />
      <div>
        {answer === "o" ? (
          <img src={Yes} alt="logo" />
        ) : (
          <img src={No} alt="logo" />
        )}
      </div>

      <br />
      <br />
      <div>
        <Collapse>
          <Panel header={"나"} key="1">
            <h4>{myDay}</h4>
            <TextArea
              rows={20}
              style={{ width: "100%", boxSizing: "border-box" }}
              value={myExplain}
            />
          </Panel>
        </Collapse>
        <br />
        <br />
        <br />
        <Collapse>
          <Panel header={"상대방"} key="2">
            <h4>{taDay}</h4>
            <TextArea
              rows={20}
              style={{ width: "100%", boxSizing: "border-box" }}
              value={taExplain}
            />
          </Panel>
        </Collapse>
      </div>
      <br />
      <br />
      <div className="luck">상대방에게 공유하고, 올해의 운세를 받아보세요.</div>
      <br />
      <br />
      <div>
        <Clipboard
          data-clipboard-text="https://saju84.choi-kang.com"
          onSuccess={onSuccess}
          className="confirm"
        >
          🚀 공유하기 →
        </Clipboard>
      </div>
      <br />
      <br />
      <div className="email-look hidden">
        <div className="email-title">
          이메일주소로 올해의 운세를 보내드립니다.
        </div>
        <br />
        <div className="box">
          <input
            type="email"
            className="input"
            placeholder="이메일주소 입력"
            name="email"
            onChange={onChange}
            value={email}
          />
          <br />
          <br />
          <div className="agree">
            *이메일 입력시 마케팅동의한것으로 간주합니다.
          </div>
          <br />
          <button className="confirm" onClick={handleClick}>
            📩 확인
          </button>
          <br />
          <br />
          <br />
        </div>
      </div>
    </div>
  );
}
export default StepThree;
