import React, { useState } from "react";
import "./StepTwo.css";
import { Input } from "antd";
import { Radio } from "antd";
import _ from "lodash";
import { useHistory } from "react-router-dom";
import axios from "axios";

function StepTwo(props) {
  const me = props.location.state.detail.me;
  const ta = props.location.state.detail.ta;
  const init = () => {
    console.log("init called");
    let tempArr = [me, ta];
    return _.shuffle(tempArr);
  };
  const [value, setValue] = useState();
  const [answer, setAnswer] = useState("-");
  const [solution, setSolution] = useState(init());

  const { TextArea } = Input;
  const history = useHistory();


  const onChange = (val, e) => {
    // 여기서 정/오답을 판별하면 되겠네요
    setValue(e.target.value);
    if (e.target.value == val) {
      setAnswer("o");
    } else {
      setAnswer("x");
    }
  };

  const handleClick = () => {
    axios
      .get(
        `https://his8t8n927.execute-api.ap-northeast-2.amazonaws.com/dev/quiz?my=${me.birth}&ta=${ta.birth}&answer=${answer}`
      )
      .then(response => {
        const result = { answer: answer, detail: props.location.state.detail };
        history.push({
          pathname: "/StepThree",
          state: { data: result }
        });
      });
  };

  // A: init[0], B: init[1]
  // init[0] = {birth: "1912-1-3", day: "갑자", explain: "xxxxxxx"}
  // init[1] = {birth: "1923-3-5", day: "신해", explain: "yyyyyyy"}
  // radio group의 value 에 각각 init[0].day, init[1].day 할당
  // 각 radio 버튼에 크로스로 init[0].day와 init[1].day 할당해서 그룹의 값과 선택된 radio 버튼의
  // 값이 일치하면 정답, 아니면 오답 => 이게 아니네요 ㅎㅎ 섞인 애가 아니라 실제 답에 해당하는 애들을 넣어야 겠네요.

  return (
    <div className="container">
      <div className="container">
        <br />
        <br />
        <div className="title">step 2</div>
        <br />
        <br />
        <div className="title-name">
          성격의 결과를 읽어보고
          <br />
          누구의 것인지 맞춰보세요!
        </div>
        <br />
        <TextArea
          rows={7}
          value={solution[0].explain}
          style={{ width: "100%", boxSizing: "border-box" }}
        />
        <br />
        <br />
        <div>
          <Radio.Group
            onChange={e => onChange(solution[0].day, e)}
            value={value}
          >
            <Radio value={me.day}>나</Radio>
            <Radio value={ta.day}>상대방</Radio>
          </Radio.Group>
        </div>
        <br />
        <br />
        <div>
          <TextArea
            rows={7}
            style={{ width: "100%", boxSizing: "border-box" }}
            value={solution[1].explain}
          />
        </div>
        <br />
        <div>
          <Radio.Group
            onChange={e => onChange(solution[1].day, e)}
            value={value}
          >
            <Radio value={ta.day}>나</Radio>
            <Radio value={me.day}>상대방</Radio>
          </Radio.Group>
        </div>
        <br />
        <br />
        <br />
        <div>
          <button className="confirm" onClick={handleClick}>
            정답 제출 👉
          </button>
        </div>
      </div>
    </div>
  );
}

export default StepTwo;
